
html {
  font-family: 'Roboto', sans-serif;
}
input {
  width: 13%;
  margin: 6px 5px;
  padding: 10px;
  border: none;
  border-bottom: 2px solid #ffcc2f;
  text-align: center;
  background: none;
}
p {
  margin: 4px 0;
  color: #ef5734;
}
label {
  color: #ef5734;
}
input:focus {
  border: none;
  border-radius: 5px;
}
.helpinfo {
  display: table;
  width: 100%;
  margin: 10px auto;
  padding-top: 10px;
  /*border-radius: 5px;*/
}
.row {
  display: table-row;
}
.cell {
  display: table-cell;
  text-align: left;
  padding-bottom: 10px;
}
.cell:first-child {
  padding: 0 20px;
}
button {
  padding: 10px 14px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid black;
}
.helpToggle {
  background: none;
  border: none;
  font-weight: 900;
  padding: 0;
  color: #fdbd10;
  font-size: 150%;
  margin: 0;
}
.adjustment {
}
.halfbox {
  display: inline-block;
  width: 45%;
}
.reverse {
  padding: 0;
  margin: 0;
  font-size: 140%;
  background: none;
  border: none;
  color: #76b852;
}
.container {
  width: 320px;
  margin: 0 auto;
}
.box {
  box-shadow: 1px 1px 3px 0px;
  border-radius: 4px;
  padding: 5px 10px;
  margin: 10px 0;
  /*background: #f4f7f9;*/
}
.sqf {
  font-size: 70%;
  vertical-align: text-top;
}
.area-box {
  border: 1px solid black;
  border-radius: 4px;
  padding:10px 0;
  margin-bottom: 6px;
  /*box-shadow: 1px 1px 4px 0px;*/
}
.decimal-input {
  width: 10%;
}
.results {
  text-align: left;
  font-size: 120%;
}
.cost .offset {
  width: 80px;
}




